<template>
  <el-dialog title="命令模板详情">
    <el-form class="the-item-detail-dialog" label-width="128px">
      <el-form-item label="标题"> {{ item.title }} </el-form-item>
      <el-form-item label="描述"> {{ item.description }} </el-form-item>

      <el-form-item label="ID"> {{ item.id }} </el-form-item>
      <el-form-item label="UUID"> {{ item.uuid }} </el-form-item>
      <el-form-item label="名称"> {{ item.name }} </el-form-item>
      <el-form-item label="分组"> {{ item.group }} </el-form-item>
      <el-form-item label="选择表达式"> {{ item.selector }} </el-form-item>

      <el-form-item label="命令"> {{ item.command }} </el-form-item>
      <el-form-item label="参数"> {{ item.args }} </el-form-item>
      <el-form-item label="工作目录"> {{ item.wd }} </el-form-item>
      <el-form-item label="环境变量"> {{ item.env }} </el-form-item>
    </el-form>

    <div>
      <ElButton type="default" @click="duplicate">创建副本</ElButton>
      <ElButton type="default" @click="close">关闭</ElButton>
    </div>
  </el-dialog>
</template>

<script>
import { ElButton } from "element-plus";

export default {
  name: "settings-intent-templates-item-detail-dialog",
  components: { ElButton },

  computed: {
    item() {
      let o = this.target;
      if (o == null) {
        o = this.emptyItem;
      }
      return o;
    },
  },

  data() {
    return {
      emptyItem: {},
    };
  },

  methods: {
    close() {
      this.$emit("update:modelValue", false);
    },

    duplicate() {
      let item = this.item;
      this.$store.dispatch("intent_template/duplicate", { item }).then(() => {
        this.close();
      });
    },
  },

  props: {
    target: Object,
  },
};
</script>

<style scoped>
.the-item-detail-dialog {
  text-align: center;
}
</style>
